import fetch from 'node-fetch';
import config from '../../../config';

const { CLIENT_ID, CLIENT_SECRET, GATEWAY_URL, AUTHORIZATION } = config;
const LOGIN_API = '/authentication/v1/partner';
const TOKEN_API = '/customer/hc-oauth2/token';

const createBody = (userName, password) =>
  JSON.stringify({
    responseType: 'code',
    scope: 'financing',
    clientId: CLIENT_ID,
    credentials: { userName, password },
  });

const fetchAuthorizationCode = async (username, password) => {
  const result = await fetch(`${GATEWAY_URL}${LOGIN_API}`, {
    method: 'POST',
    body: createBody(username, password),
    headers: {
      'Content-Type': 'application/json',
      Authorization: AUTHORIZATION,
    },
    timeout: 10000,
  });
  const json = await result.json();
  return {
    status: result.status,
    data: json,
  };
};

const fetchToken = async code => {
  const grantType = 'authorization_code';
  const clientId = CLIENT_ID;
  const clientSecret = CLIENT_SECRET;
  const result = await fetch(`${GATEWAY_URL}${TOKEN_API}`, {
    method: 'POST',
    body: `grant_type=${grantType}&client_id=${clientId}&client_secret=${clientSecret}&code=${code}`,
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded',
    },
    timeout: 10000,
  });
  const token = await result.json();
  return token;
};

export default async (req, res) => {
  const {
    body: { username, password },
  } = req;

  try {
    const result = await fetchAuthorizationCode(username, password);
    if (result.status === 422) {
      res.status(401);
      res.send(result.data);
    } else if (result.status === 401) {
      res.status(401);
      delete result.data.path;
      res.send({
        errors: [result.data],
      });
    } else {
      const code = /code=([a-z0-9]+)/.exec(result.data.redirectUri)[1];
      const token = await fetchToken(code);
      res.send(token);
    }
  } catch (err) {
    res.status(500);
    res.set('Content-Type', 'application/json');
    res.send(
      JSON.stringify({
        errors: [
          {
            code: 'INTERNAL ERROR',
            message: `Cannot login: ${err.message}`,
            severity: 'ERROR',
          },
        ],
      }),
    );
  }
};
