import { fetchToken } from './action';

const initialState = {
  token: {},
  message: '',
  loading: false,
  userInfo: {},
};

export const login = (state = initialState, action) => {
  switch (action.type) {
    case fetchToken.START: {
      return {
        ...state,
        loading: true,
      };
    }
    case fetchToken.SUCCESS: {
      return {
        ...state,
        token: action.payload,
        message: '',
        loading: false,
        userInfo: {
          userCode: '123456',
          assignedShops: [
            {
              shopCode: '000001',
              shopName: 'Cửa hàng 1',
            },
            {
              shopCode: '000002',
              shopName: 'Cửa hàng 2',
            },
            {
              shopCode: '000003',
              shopName: 'Cửa hàng 3',
            },
            {
              shopCode: '000004',
              shopName: 'Cửa hàng 4',
            },
            {
              shopCode: '000005',
              shopName: 'Cửa hàng 5',
            },
          ],
        },
      };
    }
    case fetchToken.FAIL: {
      return {
        ...state,
        token: {},
        message: action.payload,
        loading: false,
      };
    }
    default:
      return state;
  }
};
