import fs from 'fs-extra';
import path from 'path';
import infra from './env/infra.json';
import nonprod from './env/nonprod.json';

const { BROWSER, HC_ENV, PORT, TRUST_PROXY, JWT_SECRET } = process.env;
if (BROWSER) {
  throw new Error('Do not import `config` from inside the client-side code.');
}

let env = {};
const customConfig = path.join(__dirname, 'config.json');
if (fs.existsSync(customConfig)) {
  env = JSON.parse(fs.readFileSync(customConfig, 'utf8'));
} else if (HC_ENV === 'infra') {
  env = infra;
} else {
  env = nonprod;
}

export default {
  ...env,
  port: PORT || 3000,
  trustProxy: TRUST_PROXY || 'loopback',
  locales: [
    /* @intl-code-template '${lang}-${COUNTRY}', */
    'en-US',
    'cs-CZ',
    'vi-VN',
    /* @intl-code-template-end */
  ],
  auth: {
    jwt: { secret: JWT_SECRET || 'Home Credit' },
  },
  analytics: {
    googleTrackingId: 'UA-127883149-2',
  },
  api: {
    serverUrl: '',
  },
};
