const ACCESS_TOKEN = 'access_token';

const setCookie = ({ name, value, expire }) => {
  const timeExpire = new Date();
  timeExpire.setTime(timeExpire.getTime() + expire * 1000);
  document.cookie = `${name}=${value};expires=${timeExpire.toUTCString()};path=/`;
};

const getCookie = name => {
  const escape = str => str.replace(/([.*+?^${}()|[]\/\\])/g, '\\$1');
  const matchCookie = document.cookie.match(
    RegExp(`(?:^|;\\s*)${escape(name)}=([^;]*)`),
  );
  return matchCookie ? matchCookie[1] : null;
};

const createCookieLogin = (valCookie, expire) => {
  const arrInfo = [{ name: ACCESS_TOKEN, value: valCookie[ACCESS_TOKEN] }];
  arrInfo.forEach(({ name, value }) => {
    setCookie({ name, value, expire });
  });
};

export { ACCESS_TOKEN, setCookie, getCookie, createCookieLogin };
