import { createFetchAction } from 'utils/actionCreators';
import { replace } from 'middleware/location';
import { ACCESS_TOKEN, createCookieLogin } from '../../utils/handleCookie';
import { convertMessage } from './convertMessage';

export const fetchToken = createFetchAction('TOKEN');

export const login = ({ username, password }) => async dispatch => {
  dispatch(fetchToken.start());
  const body = await fetch('/api/login', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({ username, password }),
  });
  const response = await body.json();
  const { errors, access_token: accessToken, expires_in: expire } = response;
  if (Array.isArray(errors) && errors.length > 0) {
    const { code } = errors[0];
    return dispatch(fetchToken.fail(convertMessage(code)));
  }
  if (__DEV__) {
    createCookieLogin({ [ACCESS_TOKEN]: accessToken }, expire);
  }
  dispatch(fetchToken.success(response));
  return dispatch(replace('/report'));
};
