import {
  SET_LOCALE_START,
  SET_LOCALE_SUCCESS,
  SET_LOCALE_ERROR,
} from '../constants';

export const intl = (state = null, action) => {
  if (state === null) {
    return {
      initialNow: Date.now(),
      locale: 'en-US',
    };
  }

  switch (action.type) {
    case SET_LOCALE_START: {
      const { locale: localePayload } = action.payload;
      const locale = state[localePayload] ? localePayload : state.locale;
      return {
        ...state,
        locale,
        newLocale: localePayload,
      };
    }

    case SET_LOCALE_SUCCESS: {
      const { locale: localePayload } = action.payload;
      return {
        ...state,
        locale: localePayload,
        newLocale: null,
        messages: {
          ...state.messages,
          [localePayload]: action.payload.messages,
        },
      };
    }

    case SET_LOCALE_ERROR: {
      return {
        ...state,
        newLocale: null,
      };
    }

    default: {
      return state;
    }
  }
};
