import React, { PureComponent, createRef } from 'react';
import { func } from 'prop-types';
import { noop } from 'lodash';
import hoistStatics from 'hoist-non-react-statics';

const contextTypes = {
  insertCss: func,
};

export const withStyles = (...styles) => ComposedComponent => {
  class WithStyles extends PureComponent {
    constructor(props) {
      super(props);
      this.wrappedInstance = createRef();
      this.removeCss = null;
    }

    componentWillUnmount() {
      if (this.removeCss) {
        setTimeout(this.removeCss, 0);
      }
    }

    getWrappedInstance = () => this.wrappedInstance.current;

    render() {
      this.removeCss = !this.removeCss
        ? this.context.insertCss(...styles)
        : noop;
      return <ComposedComponent ref={this.wrappedInstance} {...this.props} />;
    }
  }

  const displayName =
    ComposedComponent.displayName || ComposedComponent.name || 'Component';

  WithStyles.displayName = `WithStyles(${displayName})`;
  WithStyles.contextTypes = contextTypes;
  WithStyles.ComposedComponent = ComposedComponent;

  return hoistStatics(WithStyles, ComposedComponent);
};
