import { parse } from 'query-string';
import { createNormalAction } from 'utils/actionCreators';
import {
  PUSH,
  REPLACE,
  GO,
  GO_BACK,
  GO_FORWARD,
  LOCATION_CHANGE,
} from './constants';

export const push = createNormalAction(PUSH, true);
export const replace = createNormalAction(REPLACE, true);
export const go = createNormalAction(GO, true);
export const goBack = createNormalAction(GO_BACK, false);
export const goForward = createNormalAction(GO_FORWARD, false);

export const locationChange = ({ pathname, search, hash, key, action }) => ({
  type: LOCATION_CHANGE,
  payload: {
    pathname,
    search,
    queries: parse(search),
    hash,
    key,
    action,
  },
});
