import forEach from 'lodash/map';
import isEmpty from 'lodash/isEmpty';
import { MESSAGES } from './constants';

export const validate = ({ tabData }) => {
  if (tabData) {
    const errors = {};
    forEach(tabData, (tab, tabId) => {
      if (tab.hasFinancialPartner) {
        const tabError = {};
        let selected = 0;
        forEach(tab.competitors, (competitor, name) => {
          if (competitor.selectedCompetitor) {
            selected += 1;
            if (competitor.qtyOfContract === '') {
              tabError[name] = MESSAGES.REQUIRE_ERROR;
            }
          }
        });
        if (selected === 0 || !isEmpty(tabError)) {
          errors[tabId] = tabError;
        }
      }
    });
    return errors;
  }
  return {};
};
