import React from 'react';
import { func } from 'prop-types';
import { connect } from 'react-redux';
import { fetchToken } from '../../routes/login/action';
import { ACCESS_TOKEN, getCookie } from '../../utils/handleCookie';

class CookieLoginDev extends React.PureComponent {
  static propTypes = {
    fetchToken: func,
  };

  static defaultProps = {
    fetchToken: () => {},
  };

  componentDidMount() {
    const accessToken = getCookie(ACCESS_TOKEN);
    if (accessToken) {
      this.props.fetchToken({
        [ACCESS_TOKEN]: accessToken,
      });
    }
  }

  render() {
    return null;
  }
}

const mapDispatchToProps = dispatch => ({
  fetchToken(test) {
    dispatch(fetchToken.success(test));
  },
});

export default connect(
  null,
  mapDispatchToProps,
)(CookieLoginDev);
