import isEmpty from 'lodash/isEmpty';
import forEach from 'lodash/forEach';
import set from 'lodash/fp/set';
import {
  INIT_REPORT,
  CHANGE_DATA,
  CHANGE_TAB_DATA,
  CHANGE_COMP_DATA,
  SWITCH_TAB,
  CLOSE_MODAL,
  VALIDATED,
} from './action';
import { COMPETITORS } from './constants';

const generateTab = ({ shopCode, shopName }) => {
  const competitors = {};
  COMPETITORS.forEach(competitor => {
    competitors[competitor] = {
      selectedCompetitor: true,
      qtyOfContract: '',
    };
  });
  return {
    title: shopCode,
    shopCode,
    shopName,
    hasFinancialPartner: true,
    otherCompetitors: '',
    competitors,
  };
};

const generateTabs = assignedShops => {
  const tabData = {};
  forEach(assignedShops, shop => {
    tabData[shop.shopCode] = generateTab(shop);
  });
  return tabData;
};

const compReportState = {
  reportDate: new Date(),
  isAvailable: true,
  activeTab: '',
  tabData: {},
  errors: {},
  isModalOpen: false,
};

export default (state = compReportState, action) => {
  const { type, payload } = action;
  switch (type) {
    case INIT_REPORT: {
      const tabData = generateTabs(payload);
      return set(
        'activeTab',
        payload[0].shopCode,
        set('tabData', tabData, state),
      );
    }
    case CHANGE_DATA: {
      const { name, value } = payload;
      return set(name, value, state);
    }
    case CHANGE_TAB_DATA: {
      const { tabId, name, value } = payload;
      return set(`tabData[${tabId}][${name}]`, value, state);
    }
    case SWITCH_TAB: {
      return set('activeTab', payload, state);
    }
    case CHANGE_COMP_DATA: {
      const { tabId, competitor, name, value } = payload;
      return set(
        `tabData[${tabId}].competitors[${competitor}][${name}]`,
        value,
        state,
      );
    }
    case CLOSE_MODAL: {
      return set(['isModalOpen'], false, state);
    }
    case VALIDATED: {
      return set(
        'errors',
        payload,
        set(['isModalOpen'], !isEmpty(payload), state),
      );
    }
    default:
      return state;
  }
};
