export const convertMessage = code => {
  switch (code) {
    case 'INVALID_USERNAME_PASSWORD':
    case 'INVALID_PASSWORD':
      return 'Sai tên đăng nhập/mật khẩu.';
    case 'INVALID_PASSWORD_AND_START_TEMPORARILY_BLOCK':
    case 'USER_TEMPORARILY_BLOCKED':
      return 'Đăng nhập tạm khóa. Vui lòng thử lại sau 10 phút';
    case 'INTERNAL ERROR':
      return 'Không thể đăng nhập.';
    default:
      return 'Opps...';
  }
};
