export const createFetchAction = entity => {
  const START = `FETCH_${entity}_START`;
  const SUCCESS = `FETCH_${entity}_SUCCESS`;
  const FAIL = `FETCH_${entity}_FAIL`;
  return {
    START,
    start(payload) {
      return { type: START, payload };
    },
    SUCCESS,
    success(payload) {
      return { type: SUCCESS, payload };
    },
    FAIL,
    fail(payload) {
      return { type: FAIL, payload };
    },
  };
};

export const createFormAction = formName => {
  const SUBMIT = `${formName}_SUBMIT`;
  const VALIDATE = `${formName}_VALIDATE`;
  const UPDATE = `${formName}_UPDATE`;
  return {
    SUBMIT,
    submit(payload) {
      return { type: SUBMIT, payload };
    },
    VALIDATE,
    validate(payload) {
      return { type: VALIDATE, payload };
    },
    UPDATE,
    update(payload) {
      return { type: UPDATE, payload };
    },
  };
};

export const createNormalAction = (type, includePayload) => payload => {
  const action = { type };
  if (includePayload) action.payload = payload;
  return action;
};
