import { replace } from 'middleware/location';
import isEmpty from 'lodash/isEmpty';
import { createNormalAction } from 'utils/actionCreators';
import { validate } from './validation';

export const INIT_REPORT = 'competition_report/INIT_REPORT';

export const CHANGE_DATA = 'competition_report/CHANGE_DATA';
export const CHANGE_TAB_DATA = 'competition_report/CHANGE_TAB_DATA';
export const SWITCH_TAB = 'competition_report/SWITCH_TAB';
export const CHANGE_COMP_DATA = 'competition_report/CHANGE_COMPETITOR_DATA';
export const CLOSE_MODAL = 'competition_report/CLOSE_MODAL';
export const VALIDATED = 'competition_report/VALIDATED';

export const initReport = createNormalAction(INIT_REPORT, true);
export const changeData = createNormalAction(CHANGE_DATA, true);
export const changeTabData = createNormalAction(CHANGE_TAB_DATA, true);
export const changeCompetitorData = createNormalAction(CHANGE_COMP_DATA, true);
export const switchTab = createNormalAction(SWITCH_TAB, true);
export const closeModal = createNormalAction(CLOSE_MODAL, false);
export const validated = createNormalAction(VALIDATED, true);

export const submit = () => (dispatch, getState) => {
  const state = getState().pages.report;
  if (state.isAvailable) {
    const errors = validate(state);
    dispatch(validated(errors));
    if (isEmpty(errors)) {
      dispatch(replace('/thank'));
    }
  } else {
    dispatch(replace('/thank'));
  }
};
