import { LOCATION_CHANGE } from './constants';

const locationState = {
  pathname: '/',
  search: '',
  queries: {},
  hash: '',
  key: '',
};

export const locationReducer = (state = locationState, action) => {
  if (action.type === LOCATION_CHANGE) {
    return {
      ...state,
      ...action.payload,
    };
  }
  return state;
};
